@media (max-width: 991.98px) {
  .sold__header .link-arrow {
    text-align: center;
  }
}

.sold__heading {
  line-height: 1.15;
  margin-bottom: 0;
}

@media (max-width: 991.98px) {
  .sold__heading {
    text-align: center;
    line-height: 1.15;
    font-size: 26px;
  }
}

.sold__wrapper {
  margin-top: 3.75rem;
}

@media (max-width: 991.98px) {
  .sold__wrapper {
    margin-top: 0.9375rem;
  }
}

.sold__content {
  margin-top: 2.1875rem;
  margin-bottom: 2.8125rem;
}

.sold__img img {
  width: 100%;
}

@media (max-width: 991.98px) {
  .sold__img {
    margin-bottom: 1.5625rem;
  }
}

.sold__info {
  background: #e7e4e2;
  display: flex;
  height: 100%;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0rem 5rem;
}

@media (max-width: 991.98px) {
  .sold__info {
    padding: 2rem 1.25rem;
  }
}

.sold__subheading {
  font-weight: bold;
  font-size: 2.5rem;
  line-height: 1.15;
  margin-bottom: 1.5625rem;
}

.sold__continue {
  color: #999691;
  font-weight: bold;
  font-size: 1.25rem;
  line-height: 1.3;
  margin-bottom: 2.1875rem;
  width: 100%;
}

.sold__continue::after {
  content: '';
  width: 370px;
  display: block;
  height: 1px;
  background-color: white;
  margin-top: 1.5625rem;
}

.sold__relevant {
  font-weight: bold;
  font-size: 0.9375rem;
}

.sold__btn {
  display: inline-block;
  margin-top: 1.5625rem;
  font-size: 0.8125rem;
}

@media (max-width: 991.98px) {
  .sold__btn {
    display: block;
  }
}
